<template>
	<div
		class="container"
		:class="{ 'b-container': !showSignUp, 'is-txl is-z200': showSignUp }"
		id="dynamic-container">
		<form
			class="form"
			id="b-form"
			method=""
			action="">
			<h2 class="form_title title">Sign in to Website</h2>
			<div class="form__icons">
				<img
					class="form__icon"
					src=" " />
				<img
					class="form__icon"
					src=" " />
				<img
					class="form__icon"
					src=" " />
			</div>
			<span class="form__span">or use your email account</span>
			<!-- autocomplete="off"可以让浏览器不再要求你必须使用from显示警告 -->
			<input
				class="form__input"
				type="text"
				placeholder="用户名"
				v-model="loginForm.username"
				autocomplete="off" />
			<input
				class="form__input"
				type="password"
				placeholder="密码"
				v-model.lazy="loginForm.password"
				autocomplete="off" />
			<a
				class="form__link"
				@click.prevent="goToBackend"
				>进入后台管理</a
			>
			<!-- :to="{ path: './page-header.html' }"同样可以实现跳转 -->
			<button
				class="form__button button submit"
				@click.prevent="goToIndex">
				登录
			</button>
		</form>
	</div>
</template>
<script setup lang="ts">
	import { computed, onMounted, ref } from 'vue';
	import { useStore } from 'vuex';
	import { useRouter } from 'vue-router';

	const loginForm = ref({
		username: '',
		password: '',
	});

	const store = useStore();
	const showSignUp = computed(() => store.getters.showSignUp);

	//路由跳转+通知
	import { ElNotification } from 'element-plus';
	const router = useRouter();
	const goToIndex = () => {
		// 检查用户名和密码是否都为空
		if (
			loginForm.value.username.trim() === '' &&
			loginForm.value.password.trim() === ''
		) {
			ElNotification({
				title: '提示',
				message: '请填写账户和密码',
				type: 'warning', // 使用警告类型的通知
			});
		} else if (
			loginForm.value.username === 'demo' &&
			loginForm.value.password === 'demo'
		) {
			// 如果用户名和密码正确，则跳转到index页面
			router.push('/front/index');
			ElNotification({
				title: '登录提示',
				message: '登录成功',
				type: 'success',
			});
		} else {
			// 如果用户名或密码错误，则显示登录失败的通知
			ElNotification({
				title: '登录失败',
				message: '账号或密码错误',
				type: 'error',
			});
		}
	};
	//后台跳转
	const goToBackend = () => {
		router.push('/admin/login');
	};

	//临时通行证
	onMounted(() => {
		ElNotification({
			title: '前台临时通行证',
			message:
				'账号:&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;demo<br>密码:&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;demo',
			type: 'success',
			dangerouslyUseHTMLString: true, // 允许消息内容包含HTML
		});
	});
</script>

<style scoped>
	::after,
	::before {
		margin: 0;
		padding: 0;
		box-sizing: border-box;
		user-select: none;
	}

	/* Generic */
	body {
		width: 100%;
		height: 100vh;
		display: flex;
		justify-content: center;
		align-items: center;
		font-family: 'Montserrat', sans-serif;
		font-size: 12px;
		background-color: #ecf0f3;
		color: #a0a5a8;
	}

	.container {
		display: flex;
		justify-content: center;
		align-items: center;
		position: absolute;
		top: 0;
		width: 600px;
		height: 100%;
		padding: 25px;
		background-color: #ecf0f3;
		transition: 1.25s;
	}

	.form {
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
		width: 100%;
		height: 100%;
	}

	.form__icon {
		object-fit: contain;
		width: 30px;
		margin: 0 5px;
		opacity: 0.5;
		transition: 0.15s;
	}

	.form__icon:hover {
		opacity: 1;
		transition: 0.15s;
		cursor: pointer;
	}

	.form__input {
		width: 350px;
		height: 40px;
		margin: 4px 0;
		padding-left: 25px;
		font-size: 13px;
		letter-spacing: 0.15px;
		border: none;
		outline: none;
		font-family: 'Montserrat', sans-serif;
		background-color: #ecf0f3;
		transition: 0.25s ease;
		border-radius: 8px;
		box-shadow: inset 2px 2px 4px #d1d9e6, inset -2px -2px 4px #f9f9f9;
	}

	.form__input:focus {
		box-shadow: inset 4px 4px 4px #d1d9e6, inset -4px -4px 4px #f9f9f9;
	}

	.form__span {
		margin-top: 30px;
		margin-bottom: 12px;
	}

	.form__link {
		color: #181818;
		font-size: 15px;
		margin-top: 25px;
		border-bottom: 1px solid #a0a5a8;
		line-height: 2;
	}

	.title {
		font-size: 34px;
		font-weight: 700;
		line-height: 3;
		color: #181818;
	}

	.button {
		width: 180px;
		height: 50px;
		border-radius: 25px;
		margin-top: 50px;
		font-weight: 700;
		font-size: 14px;
		letter-spacing: 1.15px;
		background-color: #4b70e2;
		color: #f9f9f9;
		box-shadow: 8px 8px 16px #d1d9e6, -8px -8px 16px #f9f9f9;
		border: none;
		outline: none;
	}

	/**/

	.b-container {
		left: calc(100% - 600px);
		z-index: 0;
	}

	/**/

	.is-txl {
		left: 0;
		transition: 1.25s;
		transform-origin: right;
	}

	.is-z200 {
		z-index: 200;
		transition: 1.25s;
	}
</style>
