<template>
	<div
		class="switch"
		id="switch-cnt">
		<div class="switch__circle"></div>
		<div class="switch__circle switch__circle--t"></div>
		<div
			class="switch__container"
			id="switch-c1">
			<h2 class="switch__title title">Welcome Back !</h2>
			<p class="switch__description description">
				This is the backend management system for your study notes
				website
			</p>
			<button
				class="switch__button button switch-btn"
				@click="change(1)">
				注册
			</button>
		</div>
		<div
			class="switch__container is-hidden"
			id="switch-c2">
			<h2 class="switch__title title">Hello Friend !</h2>
			<p class="switch__description description">
				Enter your personal details and start journey with us
			</p>
			<button
				class="switch__button button switch-btn"
				@click="change(0)">
				登录
			</button>
		</div>
	</div>
</template>

<script setup lang="ts">
	import { useStore } from 'vuex';
	import { ElNotification } from 'element-plus';

	const store = useStore();

	const change = (id: number) => {
		const switchC1 = document.querySelector('#switch-c1') as any;
		const switchC2 = document.querySelector('#switch-c2') as any;
		const switchCircle = document.querySelectorAll(
			'.switch__circle'
		) as any;
		const switchCtn = document.querySelector('#switch-cnt') as any;
		switchCtn.classList.add('is-gx');
		setTimeout(function () {
			switchCtn.classList.remove('is-gx');
		}, 1500);
		switchCtn.classList.toggle('is-txr');
		switchCircle[0].classList.toggle('is-txr');
		switchCircle[1].classList.toggle('is-txr');
		switchC1.classList.toggle('is-hidden');
		switchC2.classList.toggle('is-hidden');
		store.commit('toggleShowSignUp');

		if (id === 1) {
			ElNotification({
				title: '提示',
				message: '注册功能暂未开放，敬请期待！',
				type: 'warning', // 使用警告类型的通知
			});
		}
	};
</script>

<style scoped>
	::after,
	::before {
		margin: 0;
		padding: 0;
		box-sizing: border-box;
		user-select: none;
	}

	/* Generic */
	body {
		width: 100%;
		height: 100vh;
		display: flex;
		justify-content: center;
		align-items: center;
		font-family: 'Montserrat', sans-serif;
		font-size: 12px;
		background-color: #ecf0f3;
		color: #a0a5a8;
	}

	.title {
		font-size: 34px;
		font-weight: 700;
		line-height: 3;
		color: #181818;
	}

	.description {
		font-size: 14px;
		letter-spacing: 0.25px;
		text-align: center;
		line-height: 1.6;
	}

	.button {
		width: 180px;
		height: 50px;
		border-radius: 25px;
		margin-top: 50px;
		font-weight: 700;
		font-size: 14px;
		letter-spacing: 1.15px;
		background-color: #4b70e2;
		color: #f9f9f9;
		box-shadow: 8px 8px 16px #d1d9e6, -8px -8px 16px #f9f9f9;
		border: none;
		outline: none;
	}

	.switch {
		display: flex;
		justify-content: center;
		align-items: center;
		position: absolute;
		top: 0;
		left: 0;
		height: 100%;
		width: 400px;
		padding: 50px;
		z-index: 200;
		transition: 1.25s;
		background-color: #ecf0f3;
		overflow: hidden;
		box-shadow: 4px 4px 10px #d1d9e6, -4px -4px 10px #f9f9f9;
	}

	.switch__circle {
		position: absolute;
		width: 500px;
		height: 500px;
		border-radius: 50%;
		background-color: #ecf0f3;
		box-shadow: inset 8px 8px 12px #d1d9e6, inset -8px -8px 12px #f9f9f9;
		bottom: -60%;
		left: -60%;
		transition: 1.25s;
	}

	.switch__circle--t {
		top: -30%;
		left: 60%;
		width: 300px;
		height: 300px;
	}

	.switch__container {
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
		position: absolute;
		width: 400px;
		padding: 50px 55px;
		transition: 1.25s;
	}

	.switch__button {
		cursor: pointer;
	}

	.switch__button:hover {
		box-shadow: 6px 6px 10px #d1d9e6, -6px -6px 10px #f9f9f9;
		transform: scale(0.985);
		transition: 0.25s;
	}

	.switch__button:active,
	.switch__button:focus {
		box-shadow: 2px 2px 6px #d1d9e6, -2px -2px 6px #f9f9f9;
		transform: scale(0.97);
		transition: 0.25s;
	}

	/**/
	.is-txr {
		left: calc(100% - 400px);
		transition: 1.25s;
		transform-origin: left;
	}

	.is-z200 {
		z-index: 200;
		transition: 1.25s;
	}

	.is-hidden {
		visibility: hidden;
		opacity: 0;
		position: absolute;
		transition: 1.25s;
	}

	.is-gx {
		animation: is-gx 1.25s;
	}

	@keyframes is-gx {
		0%,
		10%,
		100% {
			width: 400px;
		}

		30%,
		50% {
			width: 500px;
		}
	}
</style>
